import React, { Component } from 'react';

import './Menu.css';
import $ from "jquery";
import link from './img/link.png'
import menu from './img/menu-icon.png'
class Menu extends Component {
  render() {
   
$(function(){
$('.click').click(function(e){
  e.preventDefault;
  $('.item').slice(0, 6).slideToggle(1000) ;
  $('.item1').slice(0, 6).slideUp(1000);
  
})
  $('.click1').click(function (e) {
    e.preventDefault;
    $('.item1').slice(0,6).slideToggle(1000);
    $('.item').slice(0,6).slideUp(1000);
  })
})

    return (
   
      <div className='link'>
      <div className='clickButton'>
                <div className='button click'><img className='clickButton_img' src={menu} /></div>
                <div className='button click1'><img className='clickButton_img'src={link}/></div>
        </div>
      <div className="Container">
            <a className='item'><div className='item_content'><a href='#'>Main</a></div></a>
            <a className='item'><div className='item_content'><a href='#'>Framework</a></div></a>
            <a className='item'><div className='item_content'><a href='#'>Skills</a></div></a>
            <a className='item'><div className='item_content'><a href='#'>Story</a></div></a>
            <a className='item'><div className='item_content'><a href='#'>Feedback</a></div></a>
            <a className='item1' href='https://vk.com/vlad1552333' target='blank'><div className='item_content'><span className='icon icon_vk'></span><span>Vk</span></div></a>
            <a className='item1' href='https://github.com/Vladislav1552' target='blank'><div className='item_content'><span className='icon icon_github'></span><a href='#'>Github  </a></div></a>
            <a className='item1' href='https://www.instagram.com/vlad_vorobei3' target='blank'><div className='item_content'><span className='icon icon_instagram'></span><a href='#'>Instagram</a></div></a>
            <a className='item1' href='https://www.facebook.com/vlad.vorobyev.359' target='blank'><div className='item_content'><span className='icon icon_facebook'></span><a href='#'>Facebook</a></div></a>
         
      </div>
        </div>
   
    
    );
  }
}

export default Menu;


